.login-container {
    width: 60%;
}

.jira-logo {
    height: 100px;
    width: 200px;
}

.login-form {
    display: flex;
    align-items: center;
    flex-direction: column;
    row-gap: 10px;
}

.login-title {
    font-size: 60px;
    font-weight: bold;
}

.login-subtitle {
    font-size: 20px;
}

.login-social-accounts {
    display: flex;
    justify-content: space-between;
    width: 200px;
}

.account-logo {
    cursor: pointer;
    height: 50px;
    width: 50px;
}

.login-form-options {
    display: flex;
    width: 500px;
    align-items: center;
}

hr {
    height: 0;
    width: 200px;
}

.login-details {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    align-items: center;
}

.input-box {
    width: 450px;
    height: 25px;
}

.signin-btn {
    width: 200px;
    height: 50px;
    border-radius: 20px;
    font-size: 20px;
    cursor: pointer;
}